//公用头部
const Header = resolve => {
  require.ensure(['components/Header.vue'], () => {
    resolve(require('components/Header.vue'));
  }, 'Home');
};
//文章入口
const Article = resolve => {
  require.ensure(['components/article/Article.vue'], () => {
    resolve(require('components/article/Article.vue'));
  }, 'Article');
};
//文章列表
const ArticleList = resolve => {
  require.ensure(['components/article/ArticleList.vue'], () => {
    resolve(require('components/article/ArticleList.vue'));
  }, 'Article');
};
//文章列表
const ArticleDetail = resolve => {
  require.ensure(['components/article/ArticleDetail.vue'], () => {
    resolve(require('components/article/ArticleDetail.vue'));
  }, 'Article');
};


export const article = {
  path: '/vue/article',
  components: {
    default: Article,
  },
  children: [
    {
      path: ':userId?',
      name: "ArticleList",
      components: {
        default: ArticleList,
        'v-header': Header
      },
    },
    {
      path: '/detail/:id?',
      name: "ArticleDetail",
      components: {
        default: ArticleDetail,
        'v-header': Header
      },
    },
  ],
  meta: {isPublish: true, requireAuth: false}
}



